$sebtons-gold: #ffde59;
$grey: #fafafa;
$box-shadow: 0 0 13px rgba(0, 0, 0, 0.3);

%title {
  font-weight: 300;
  font-size: 7vmin;
  padding-bottom: 0.5vmin;
  margin-bottom: 2vmin;
  border-bottom: 0.05vmin solid $sebtons-gold;
}

%sub-title {
  font-weight: 300;
  font-size: 4vmin;
  padding-bottom: 0.5vmin;
  margin-bottom: 2vmin;
  border-bottom: 0.05vmin solid $sebtons-gold;
  transition: all 0.3s ease-in-out;
}
